import {SET_TOKEN,SET_TYPES} from '../actions/types';

const initialState = {
    subscribed: false,
    token: '',
    types: []
}

export default function(state = initialState,action) {
    switch(action.type) {
        case SET_TOKEN: {
            return {
                ...state,
                subscribed: action.payload.subscribed,
                token: action.payload.token
            }
        }
        case SET_TYPES : {
            return {
                ...state,
                types: action.payload
            }
        }
        default: 
            return state;
    }
}