import React, { Component } from 'react'
import {connect} from 'react-redux';
import {setTypes} from '../actions/authAction';
import PropTypes from 'prop-types';

class ListSubscription extends Component {
  constructor() {
    super();
    this.state = {
      types: []
    }
    this.onChangeEvent = this.onChangeEvent.bind(this);
    this.onSubmitEvent = this.onSubmitEvent.bind(this);
  }
  onChangeEvent(e) {
    if(e.target.checked) {
      let val = this.state.types;
      val.push(e.target.name);
      this.setState({types: val}); 
    } else {
      let value = this.state.types;
      value = value.filter(val => {
        return e.target.name !== val;
      });
      this.setState({types: value});
    }
  }
  onSubmitEvent(e) {
    this.props.setTypes(this.state.types)
    //send data to the server
  }
  render() {
    return (
      <div>
        <ul className="ls_main_ul">
            <li className="ls_li">
              <span className="ls_icon"></span>
              <p className="ls_head">IT MANAGER</p>
              <label className="switch">
                <input type="checkbox" name="IT_MANAGER" onChange={this.onChangeEvent}/>
                <span className="slider round"></span>
              </label>
            </li>
            <li className="ls_li">
              <span className="ls_icon"></span>
              <p className="ls_head">CODING</p>
              <label className="switch">
                <input type="checkbox" name="CODING" onChange={this.onChangeEvent}/>
                <span className="slider round"></span>
              </label>
            </li>
            <li className="ls_li">
              <span className="ls_icon"></span>
              <p className="ls_head">WEB DESIGNING</p>
              <label className="switch">
                <input type="checkbox" name="WEB_DESIGNING" onChange={this.onChangeEvent}/>
                <span className="slider round"></span>
              </label>
            </li>
            <li className="ls_li">
              <span className="ls_icon"></span>
              <p className="ls_head">TEMPLE RUN</p>
              <label className="switch">
                <input type="checkbox" name="TEMPLE_RUN" onChange={this.onChangeEvent}/>
                <span className="slider round"></span>
              </label>
            </li>
            <li className="ls_li">
              <span className="ls_icon"></span>
              <p className="ls_head">ICE BREAKER</p>
              <label className="switch">
                <input type="checkbox" name="ICE_BREAKER" onChange={this.onChangeEvent}/>
                <span className="slider round"></span>
              </label>
            </li>
            <li className="ls_li">
              <span className="ls_icon"></span>
              <p className="ls_head">PS</p>
              <label className="switch">
                <input type="checkbox" name="PS" onChange={this.onChangeEvent}/>
                <span className="slider round"></span>
              </label>
            </li>
            <li className="ls_li">
              <span className="ls_icon"></span>
              <p className="ls_head">GAMING</p>
              <label className="switch">
                <input type="checkbox" name="GAMING" onChange={this.onChangeEvent}/>
                <span className="slider round"></span>
              </label>
            </li>
        </ul>
        <div className="flex">
          <button className="btn btn-submit" onClick={this.onSubmitEvent}>Submit</button>
        </div>
      </div>
    )
  }
}

ListSubscription.propType = {
  user: PropTypes.object.isRequired,
  setTypes: PropTypes.func.isRequired
}
const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = {
  setTypes,
}
export default connect(mapStateToProps,mapDispatchToProps)(ListSubscription);